import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import PageHeader from '@/component/PageHeader'
import {
    AndroidFilled,
    EditFilled,
    DeleteFilled,
    BarChartOutlined,
    PlusOutlined, ExclamationCircleOutlined,
} from '@ant-design/icons'
import {Table, Button, message, Modal} from 'antd'
import { axiosPost} from '@/extend/axios/index.js'
import { connect } from 'react-redux'
import '@/assets/css/common.scss'
const { confirm } = Modal;
function Application(props){
    const columns = [
        {
            title: '用户编号',
            dataIndex: 'userId',
            key: 'userId',
        },
        {
            title: '手机号',
            dataIndex: 'phone',
            key: 'phone',
        },
        {
            title: '备注',
            dataIndex: 'remark',
            key: 'remark',
        },
        {
            title: '操作',
            dataIndex: 'isBlack',
            key: 'isBlack',
            // render: (text, record) => (
            //     <div className="action-button-group">
            //         <p>
            //             <Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showActionModal('add','查看',record)}}>查看</Button></p>
            //         <p> <Button className="btn-edit" icon={<EditFilled/>} onClick={() => {
            //             showAuditActionModal('add', '添加黑名单', record)
            //         }}>填写</Button></p>
            //     </div>
            // ),
            render: (text, record) => {

                    return (
                        <div className="action-button-group">
                            <p>
                                <Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showConfirm('add','删除',record)}}>删除</Button></p>
                        </div>
                    )

            },
        },

    ]; 
    const [dataList,setDataList] = useState([]);
    const [appListLoading,setAppListLoading] = useState(false);
    const [pagination,setPagination] = useState({
        current: 1,
        pageSize: 10
    });
    const getAppList = () => {
        setAppListLoading(true);
        axiosPost('/manage/wx/minipro/userblackinfo/list',{
            current: pagination.current,
            pageSize: pagination.pageSize
        }).then((res) => {
            setAppListLoading(false);
            setDataList(res.data.list);
            setPagination({
                current: res.data.pageNum,
                pageSize: res.data.pageSize,
                total: res.data.total                
            })
        }).catch((error) => {
            console.log(error);
        });
    }
    const handleTableChange = (page) => {
        setPagination({
            ...page
        })
    }
    useEffect(() => {
        getAppList();

        return () => {
            
        }
    },[pagination.current]);
    const showConfirm = (type,title,record) => {
        confirm({
            title: '确定删除吗？',
            icon: <ExclamationCircleOutlined />,
            onOk() {
                axiosPost('/manage/wx/minipro/userblackinfo/delete/'+record.id).then((res) => {
                    if(res.code == '0'){
                        message.success("操作成功");
                        getAppList();
                    }else{
                        message.error(res.msg);
                    }
                }).catch((error) => {
                    console.log(error);
                });
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }

    return (
        <div className="application-box">
            <PageHeader title="黑名单信息" icon={AndroidFilled} />
            <div className="page-body">
                <div className="panel">
                    <div className="panel-title">黑名单信息</div>
                    <div className="panel-body">
                        {/* <div className="action">
                            <Button className="btn-add" icon={<PlusOutlined />} onClick={()=>{showActionModal('add','新增应用')}}>添加应用</Button>
                        </div> */}
                        <Table
                          bordered
                          loading={appListLoading}
                          dataSource={dataList}
                          columns={columns}
                          rowKey={(row) => {
                            return row.id
                          }}
                          pagination={{
                              current: pagination.current,
                              pageSize: pagination.pageSize,
                              total: pagination.total
                          }}
                          onChange={handleTableChange}
                        />
                    </div>
                </div>
            </div>

        </div>
    )
}
const mapStateToProps = (state) => ({username: state.LoginReducer.username})
export default connect(mapStateToProps)(Application)