import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import PageHeader from '@/component/PageHeader'
import {
    AndroidFilled,
    EditFilled,
    DeleteFilled,
    BarChartOutlined,
    PlusOutlined, ExclamationCircleOutlined,
} from '@ant-design/icons'
import {Table, Button, message, Modal} from 'antd'
import { axiosPost} from '@/extend/axios/index.js'
import { connect } from 'react-redux'
import ActionModal from './component/ActionModal'
import AuditActionModal from './component/AuditActionModal'
import '@/assets/css/common.scss'
const { confirm } = Modal;
function Application(props){
    const columns = [
        {
            title: '需求编号',
            dataIndex: 'needNumber',
            key: 'needNumber',
        },
        {
            title: '用户编号',
            dataIndex: 'userId',
            key: 'userId',
        },
        {
            title: '手机号',
            dataIndex: 'phone',
            key: 'phone',
        },
        {
            title: '微信号',
            dataIndex: 'wechatNumber',
            key: 'wechatNumber',
        },
        {
            title: '姓名',
            dataIndex: 'name',
            key: 'name',
        },
        {
            title: '职位',
            dataIndex: 'position',
            key: 'position',
        },
        {
            title: '邮箱',
            dataIndex: 'email',
            key: 'email',
        },
        {
            title: '需求类别',
            dataIndex: 'needTypeName',
            key: 'needTypeName',

        },
        {
            title: '其他',
            key: 'action',
            render: (text, record) => (
                <div className="action-button-group">
                <p>
            <Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showActionModal('add','查看',record)}}>查看</Button></p>
                 </div>
                ),
        },
        {
            title: '推荐位',
                dataIndex: 'deductPoints',
            key: 'deductPoints',
            render:(text) =>{
            return text == 1 ? '是' : '否'
            }
        },

    {
            title: '当前状态',
            dataIndex: 'state',
            key: 'state',
            render:(text) =>{
                if(text == '30'){
                    return '上架'
                }else if(text == '31'){
                    return '下架'
                }
            }
        },
    {
        title: '操作',
            key: 'action',
        render: (text, record) => (
        <div className="action-button-group">
        <p>
        <Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showAuditActionModal('add','审核',record)}}>审核</Button></p>
    </div>
    ),
    },
        {
            title: '推荐',
            dataIndex: 'recommendFlag',
            key: 'recommendFlag',
            render: (text, record) => {
                if (text==0) {
                    return (
                        <div className="action-button-group">
                            <p> <Button className="btn-edit" icon={<EditFilled/>} onClick={() => {
                                pointRecommend('add', '推荐', record)
                            }}>推荐</Button></p>
                        </div>
                    )
                } else {
                    return (
                        <div className="action-button-group">
                            <p> <Button className="btn-edit" icon={<EditFilled/>} onClick={() => {
                                cancelRecommend('add', '取消推荐', record)
                            }}>取消推荐</Button></p>
                        </div>
                    )
                }
            },
        },
    ]; 
    const [dataList,setDataList] = useState([]);
    const [appListLoading,setAppListLoading] = useState(false);
    const [isShowModalVisible,setIsShowModalVisible] = useState(false);
    const [isShowAuditModalVisible,setIsShowAuditModalVisible] = useState(false);

    const [modalTitle,setModalTitle] = useState('');
    const [rowRecord,setRowRecord] = useState({});
    const [pagination,setPagination] = useState({
        current: 1,
        pageSize: 10
    });
    const getAppList = () => {
        setAppListLoading(true);
        axiosPost('/manage/wx/minipro/userNeed/list',{
            current: pagination.current,
            pageSize: pagination.pageSize,
            searchParams: '{"reviewStatus":"reviewStatus"}'
        }).then((res) => {
            setAppListLoading(false);
            setDataList(res.data.list);
            setPagination({
                current: res.data.pageNum,
                pageSize: res.data.pageSize,
                total: res.data.total                
            })
        }).catch((error) => {
            console.log(error);
        });
    }
    const handleTableChange = (page) => {
        setPagination({
            ...page
        })
    }
    const pointRecommend = (type,title,record) => {
        confirm({
            title: '确定推荐吗？',
            icon: <ExclamationCircleOutlined />,
            onOk() {
                axiosPost('/manage/wx/minipro/userecommend/save',{
                    needNumber: record.needNumber,
                    userId: record.userId,
                    needId: record.id
                }).then((res) => {
                    if(res.code == '0'){
                        message.success("操作成功");
                        hideModal();
                        getAppList();
                    }else{
                        message.error(res.message);
                    }
                }).catch((error) => {
                    console.log(error);
                });
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }
    const cancelRecommend = (type,title,record) => {
        confirm({
            title: '确定取消推荐吗？',
            icon: <ExclamationCircleOutlined />,
            onOk() {
                axiosPost('/manage/wx/minipro/userecommend/delete',{
                    needId: record.id
                }).then((res) => {
                    if(res.code == '0'){
                        message.success("操作成功");
                        hideModal();
                        getAppList();
                    }else{
                        message.error(res.message);
                    }
                }).catch((error) => {
                    console.log(error);
                });
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }
    useEffect(() => {
        getAppList();

        return () => {
            
        }
    },[pagination.current]);
    const showActionModal = (type,title,record) => {
        setIsShowModalVisible(true);
        setModalTitle(title);
        setRowRecord(record);
        setAppListLoading(true);
        axiosPost('/manage/wx/minipro/userNeed/details/'+record.id).then((res) => {
            setAppListLoading(false);
            console.log(res.data);
            setRowRecord(res.data);
        }).catch((error) => {
            console.log(error);
        });
    }
    const showAuditActionModal = (type,title,record) => {
        setIsShowAuditModalVisible(true);
        setModalTitle(title);
        setRowRecord(record);
        setAppListLoading(false);

    }
    const hideModal = () => {
        setIsShowModalVisible(false);
        setIsShowAuditModalVisible(false);
    }
    return (
        <div className="application-box">
            <PageHeader title="过审需求" icon={AndroidFilled}  />
            <div className="page-body">
                <div className="panel">
                    <div className="panel-title">过审需求</div>
                    <div className="panel-body">
                        {/* <div className="action">
                            <Button className="btn-add" icon={<PlusOutlined />} onClick={()=>{showActionModal('add','新增应用')}}>添加应用</Button>
                        </div> */}
                        <Table
                          bordered
                          loading={appListLoading}
                          dataSource={dataList}
                          columns={columns}
                          rowKey={(row) => {
                            return row.id
                          }}
                          pagination={{
                              current: pagination.current,
                              pageSize: pagination.pageSize,
                              total: pagination.total
                          }}
                          onChange={handleTableChange}
                        />
                    </div>
                </div>
            </div>
            <ActionModal visible={isShowModalVisible} title={modalTitle} record={rowRecord} hideModal={hideModal} refresh={getAppList} />
            <AuditActionModal visible={isShowAuditModalVisible} title={modalTitle} record={rowRecord} hideModal={hideModal} refresh={getAppList} />

    </div>
    )
}
const mapStateToProps = (state) => ({username: state.LoginReducer.username})
export default connect(mapStateToProps)(Application)