import React,{ useEffect } from 'react';
import {
    Switch,
    Route,
    Redirect,
    useHistory
} from "react-router-dom";
import { Layout } from 'antd';
import HeaderCustom from '@/component/Header';
import SideBar from '@/component/SideBar';
import Workbench from '@/pages/workbench';
import Application from '@/pages/application';
import Waitconfirm from '@/pages/waitconfirm';
import CommunityCode from '@/pages/communitycode';
import AllRequirements from '@/pages/allrequirements';
import PassRequirements from '@/pages/passrequirements';
import RejectRequirements from '@/pages/rejectrequirements';
import AllCommunitys from '@/pages/allcommunitys';
import AddCommunitys from '@/pages/allcommunitys/add';
import UpdateCommunitys from '@/pages/allcommunitys/add/update.js';
import UserInfo from '@/pages/userinfo';
import UserBlackInfo from '@/pages/userblackinfo';
import SystemUserManage from '@/pages/systemUserManage'
import SystemRoleManage from '@/pages/systemRoleManage'
import SystemMenuManage from '@/pages/systemMenuManage'
import SystemParamConfiguration from '@/pages/systemParamConfiguration'


import BlackList from '@/pages/blacklist';
import Channel from '@/pages/channel';
import { connect } from 'react-redux'
import { PropertySafetyFilled } from '@ant-design/icons';

const { Content } = Layout;

function HomeIndex(props){
    let history = useHistory();
    useEffect(() => {
        console.log('触发判断');
        if(!localStorage.getItem('$Token')){
            history.push('/login');
        }
    },[props.expired]);  
    return (
        <Layout>
            {/* header */}
            <HeaderCustom />
            <Layout>
            {/* sidebar */}
            <SideBar />
            <Layout style={{ padding: '40px 36px' }}>
                <Content
                style={{
                    margin: 0,
                    minHeight: 500,
                }}
                >
                    <Switch>
                        <Route path="/wx/statistics" component={Workbench}></Route>
                        <Route path="/wx/resoDocking" component={Application}></Route>
                        <Route path="/wx/allcommunitys" component={AllCommunitys}></Route>
                        <Route path="/wx/allrequirements" component={AllRequirements}></Route>
                        <Route path="/wx/waitconfirm" component={Waitconfirm}></Route>
                        <Route path="/wx/passrequirements" component={PassRequirements}></Route>
                        <Route path="/wx/rejectrequirements" component={RejectRequirements}></Route>
                        <Route path="/wx/communitycode" component={CommunityCode}></Route>
                        <Route path="/wx/addcommunitys" component={AddCommunitys}></Route>
                        <Route path="/wx/updatecommunitys" component={UpdateCommunitys}></Route>
                        <Route path="/wx/userinfo" component={UserInfo}></Route>
                        <Route path="/wx/userblackinfo" component={UserBlackInfo}></Route>
                        <Route path="/sys/sysusers" component={SystemUserManage}></Route>
                        <Route path="/sys/sysroles" component={SystemRoleManage}></Route>
                        <Route path="/sys/sysmenus" component={SystemMenuManage}></Route>
                        <Route path="/sys/paramconfiguration" component={SystemParamConfiguration}></Route>
                        <Route path="/blacklist" component={BlackList}></Route>
                        <Route path="/channel" component={Channel}></Route>
                        <Redirect to="/sys/sysusers"></Redirect>
                        {/* <Redirect to="/wx/statistics"></Redirect> */}
                    </Switch>                       
                </Content>
            </Layout>
            </Layout>
        </Layout>
    );
}
const mapStateToProps = (state) => ({menuList: state.GlobalReducer.menuList,expired: state.LoginReducer.expired})
export default connect(mapStateToProps,null)(HomeIndex)