import React, { useEffect } from 'react'
import { Select } from 'antd';
import { Modal, Form, Input, message } from 'antd'
import { axiosPost } from '@/extend/axios/index.js';
import '@/assets/css/modal.scss'
const FormItem = Form.Item;

export default function AuditActionModal(props){
    const {title,visible,record} = props;
    const [form] = Form.useForm();
    const formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 16 },
    };
    const handleOk = () => {
        form.validateFields().then(values => {
            console.log(values);
            axiosPost('/manage/wx/minipro/userblackinfo/save',{
                userId:record.id,
                phone:record.phone,
                remark:values.remark
            }).then((res) => {
                if(res.code == '0'){
                    message.success("操作成功");
                    handleCancel();
                    props.refresh();
                }else{
                    message.error(res.msg);
                }
            }).catch((error) => {
                console.log(error);
            });
        }).catch(error => {
            console.log(error);
        })
    }
    const handleCancel = () => {
        form.resetFields();
        props.hideModal();
    }
    const handleChange =(e) => { // 选择
        console.log('selected ${value}')
    }
    useEffect(() => {
        form.setFieldsValue(record);
    },[record]);
    return (
        <div>
            <Modal
                getContainer={false}
                width={500}
                className="modal-common"
                title={title}
                visible={visible}
                onOk={handleOk}
                onCancel={handleCancel}
            >
                <div className="modal-inner-connent">
                    <Form {...formItemLayout} form={form}>
                        <FormItem name="id" noStyle={true}>
                            <Input type="hidden" />
                        </FormItem>
                        <FormItem label="备注" name="remark" rules={[{  message: '请输入原因'}]}>
                            <Input placeholder="请输入原因" />
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        </div>
    )
}