import React, {useEffect, useState} from 'react'
import {Modal, Form, Input, message, Table, Button} from 'antd'
import { axiosPost } from '@/extend/axios/index.js';
import '@/assets/css/modal.scss'
import {EditFilled} from "@ant-design/icons";
const FormItem = Form.Item;

export default function ActionModal(props){
    const {title,visible,record} = props;
    const [form] = Form.useForm();
    const columns = [
        {
            title: '社群编号',
            dataIndex: 'communityNumber',
            key: 'communityNumber',
        },
        {
            title: '社群名称',
            dataIndex: 'communityName',
            key: 'communityName',
        },
    ];
    const [dataList,setDataList] = useState([]);
    const [appListLoading,setAppListLoading] = useState(false);
    const [pagination,setPagination] = useState({
        current: 1,
        pageSize: 10
    });
    const handleTableChange = (page) => {
        setPagination({
            ...page
        })
    }
    //验证手机号
    const checkMobile = (rule, value) =>{
        if (value && !/^1[3,4,5,6,7,8,9]\d{9}$/i.test(value)) {
            return Promise.reject('请输入正确的手机号');
        } else {
            return Promise.resolve();
        }
    }
    const handleOk = () => {
        props.hideModal();
    }
    const handleCancel = () => {
        props.hideModal();
    }
    const getCommunityList = () => {
        setAppListLoading(true);
        console.log(record.id)
        axiosPost('/manage/wx/minipro/user/community',{
            current: pagination.current,
            pageSize: pagination.pageSize,
            id:record.id
        }).then((res) => {
            setAppListLoading(false);
            setDataList(res.data.list);
            setPagination({
                current: res.data.pageNum,
                pageSize: res.data.pageSize,
                total: res.data.total
            })
        }).catch((error) => {
            console.log(error);
        });
    }
    useEffect(() => {
        if(record.id) {
            getCommunityList();
        }
        return () => {

        }
    },[pagination.current,record]);
    return (
        <div>
            <Modal
            getContainer={false}
            width={1000}
            height={1000}
            className="modal-common"
            title={title}
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            cancelText="关闭"
            >
                <div className="modal-inner-connent">
                    <div className="page-body">
                        <div className="panel">
                                <Table
                                    bordered
                                    loading={appListLoading}
                                    dataSource={dataList}
                                    columns={columns}
                                    rowKey={(row) => {
                                        return row.communityNumber
                                    }}
                                    pagination={{
                                        current: pagination.current,
                                        pageSize: pagination.pageSize,
                                        total: pagination.total
                                    }}
                                    onChange={handleTableChange}
                                />

                        </div>
                    </div>
                </div>
            </Modal>
        </div>
    )
}