import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import PageHeader from '@/component/PageHeader'
import {
    AndroidFilled,
    EditFilled,
    DeleteFilled,
    BarChartOutlined,
    PlusOutlined,
} from '@ant-design/icons'
import { Tree, Button,message } from 'antd'
import { axiosPost} from '@/extend/axios/index.js'
import '@/assets/css/common.scss'

function SystemUserManage(props){
    console.log(props);
    const breadcrumbRoutes = [
        {
          path: '/sys/sysmenus',
          breadcrumbName: '系统菜单管理',
        },
        {
          path: '',
          breadcrumbName: '系统管理',
        }
    ];
    const [dataList,setDataList] = useState([]);
    const [listLoading,setListLoading] = useState(false)
    const getTree = (tree = []) =>{
        const newTreelist = []        
        if(tree && tree.length !== 0){                                  
            tree.forEach((currentValue,index) => {
                const item = {}
                item.title = currentValue.label
                item.key = currentValue.value
                item.checked = currentValue.checked                               
                if(currentValue.expanded) item.children = getTree(currentValue.children)         
                newTreelist.push(item)                
            })            
        }
        return newTreelist
    }
    const getList = () => {
        setListLoading(true);
        axiosPost('/manage/sys/menu/find').then((res) => {
            if(res.code == 0){
                const tree = getTree(res.data)
                setDataList(tree);
            }            
        }).catch((error) => {
            console.log(error);
        });
    }
    useEffect(() => {
        getList()
        return () => {
            
        }
    },[]);
    return (
        <div className="application-box">
            <PageHeader title="系统菜单管理" icon={AndroidFilled} routes={breadcrumbRoutes} />
            <div className="page-body">
                <div className="panel">
                    <div className="panel-title">系统菜单管理</div>
                    <div className="panel-body">
                        <Tree 
                          checkable
                          treeData={dataList}          
                        />
                    </div>
                </div>
            </div>
        </div>
    )
}
export default SystemUserManage