import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import PageHeader from '@/component/PageHeader'
import {
    AndroidFilled,
    EditFilled,
    DeleteFilled,
    BarChartOutlined,
    PlusOutlined,
} from '@ant-design/icons'
import { Table, Button,Select,Form } from 'antd'
import { axiosPost} from '@/extend/axios/index.js'
import { connect } from 'react-redux'
import ActionModal from './component/ActionModal'
import AuditActionModal from "./component/AuditActionModal";
import '@/assets/css/common.scss'
const { Option } = Select;

function Application(props){
    const columns = [
        {
            title: '用户编号',
            width: '7%',
            dataIndex: 'id',
            key: 'id',
        },
        {
            title: '会员',
            width: '7%',
            dataIndex: 'memberState',
            render:(text) =>{
                return text === 0 ? '否' : '是'
            }
        },
        {
            title: '会员到期日',
            width: '7%',
            dataIndex: 'expirationMemberTime',
            render:(text) =>{
                return text ? text.split('T')[0] : '无'
            }
        },
        {
            title: '手机号',
            dataIndex: 'phone',
            key: 'phone',
        },
        {
            title: '微信号',
            width: '7%',
            dataIndex: 'wechatNumber',
            key: 'wechatNumber',
        },
        {
            title: '姓名',
            width: '7%',
            dataIndex: 'name',
            key: 'name',
        },
        {
            title: '职位',
            width: '7%',
            dataIndex: 'position',
            key: 'position',
        },
        {
            title: '邮箱',
            width: '7%',
            dataIndex: 'email',
            key: 'email',
        },
        {
            title: '公司名称',
            width: '7%',
            dataIndex: 'companyName',
            key: 'companyName',
        },
        {
            title: '地址',
            width: '7%',
            dataIndex: 'address',
            key: 'address',
        },
        {
            title: '当前积分',
            width: '7%',
            dataIndex: 'integral',
            key: 'integral',
        },
        {
            title: '接受协议',
            width: '7%',
            dataIndex: 'agreement',
            key: 'agreement',
            render:(text) =>{
                return text == 1 ? '是' : '否'
            }
        },
        {
            title: '需求条数',
            width: '7%',
            dataIndex: 'needs',
            key: 'needs',
        },
        {
            title: '进入社群',
            width: '9%',
            dataIndex: 'isBlack',
            // render: (text, record) => (
            //     <div className="action-button-group">
            //         <p>
            //             <Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showActionModal('add','查看',record)}}>查看</Button></p>
            //         <p> <Button className="btn-edit" icon={<EditFilled/>} onClick={() => {
            //             showAuditActionModal('add', '添加黑名单', record)
            //         }}>填写</Button></p>
            //     </div>
            // ),
            render: (text, record) => {
                if (text==0) {
                    return (
                        <div className="action-button-group">
                            <p>
                                <Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showActionModal('add','查看',record)}}>查看</Button></p>
                            <p> <Button className="btn-edit" icon={<EditFilled/>} onClick={() => {
                                showAuditActionModal('add', '添加黑名单', record)
                            }}>添加黑名单</Button></p>
                        </div>
                    )
                } else {
                    return (
                             <div className="action-button-group">
                                 <p>
                                     <Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showActionModal('add','查看',record)}}>查看</Button></p>
                                 <p>已加入黑名单</p>
                             </div>
                    )
                }
            },
        },

    ]; 
    const [dataList,setDataList] = useState([]);
    const [appListLoading,setAppListLoading] = useState(false);
    const [isShowModalVisible,setIsShowModalVisible] = useState(false);
    const [isShowAuditModalVisible,setIsShowAuditModalVisible] = useState(false);

    const [modalTitle,setModalTitle] = useState('');
    const [rowRecord,setRowRecord] = useState({});
    const [pagination,setPagination] = useState({
        current: 1,
        pageSize: 10
    });
    const getAppList = (isMember) => {
        setAppListLoading(true);
        axiosPost('/manage/wx/minipro/user/info',{
            current: pagination.current,
            pageSize: pagination.pageSize,
            type: isMember ? isMember : ''
        }).then((res) => {
            setAppListLoading(false);
            setDataList(res.data.list);
            setPagination({
                current: res.data.pageNum,
                pageSize: res.data.pageSize,
                total: res.data.total                
            })
        }).catch((error) => {
            console.log(error);
        });
    }
    const handleTableChange = (page) => {
        setPagination({
            ...page
        })
    }
    useEffect(() => {
        getAppList();

        return () => {
            
        }
    },[pagination.current]);
    const showActionModal = (type,title,record) => {
        setIsShowModalVisible(true);
        setModalTitle(title);
        setRowRecord(record);
        setAppListLoading(true);

    }
    const showAuditActionModal = (type,title,record) => {
        setIsShowAuditModalVisible(true);
        setModalTitle(title);
        setRowRecord(record);
        setAppListLoading(false);

    }
    const hideModal = () => {
        setIsShowModalVisible(false);
        setIsShowAuditModalVisible(false);
        setAppListLoading(false);
    }
    const handleChange = (value) => {
        getAppList(value)
    }
    return (
        <div className="application-box">
            <PageHeader title="用户信息" icon={AndroidFilled} />
            <div className="page-body">
                <div className="panel">
                    <div className="panel-title">用户信息</div>
                    <div className="panel-body">
                        <div className="action">
                            <div style={{float: 'right' }}>
                                <label>用户信息：</label>
                                <Select defaultValue="" style={{ width: 120}} onChange={handleChange}>
                                    <Option value="">全部</Option>
                                    <Option value="1">会员</Option>
                                </Select>
                            </div>
                        </div>
                        <Table
                          bordered
                          tableLayout="fixed"
                          loading={appListLoading}
                          dataSource={dataList}
                          columns={columns}
                          rowKey={(row) => {
                            return row.id
                          }}
                          pagination={{
                              current: pagination.current,
                              pageSize: pagination.pageSize,
                              total: pagination.total
                          }}
                          onChange={handleTableChange}
                        />
                    </div>
                </div>
            </div>
            <ActionModal visible={isShowModalVisible} title={modalTitle} record={rowRecord} hideModal={hideModal} refresh={getAppList} />
            <AuditActionModal visible={isShowAuditModalVisible} title={modalTitle} record={rowRecord} hideModal={hideModal} refresh={getAppList} />

        </div>
    )
}
const mapStateToProps = (state) => ({username: state.LoginReducer.username})
export default connect(mapStateToProps)(Application)