import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import PageHeader from '@/component/PageHeader'
import {
    AndroidFilled,
    EditFilled,
    DeleteFilled,
    BarChartOutlined,
    PlusOutlined,
} from '@ant-design/icons'
import { Table, Button } from 'antd'
import { axiosPost} from '@/extend/axios/index.js'
import { connect } from 'react-redux'
import ActionModal from './component/ActionModal'
import '@/assets/css/common.scss'

function Application(props){
    console.log(props);
    const breadcrumbRoutes = [
        {
          path: '/application',
          breadcrumbName: '应用管理',
        },
        {
          path: '',
          breadcrumbName: '应用列表',
        }
    ];
    const columns = [
        {
            title: '公司全称',
            dataIndex: 'companyName',
            key: 'companyName',
        },
        {
            title: '公司职务',
            dataIndex: 'position',
            key: 'position',
        },
        {
            title: '合作身份',
            dataIndex: 'cooperateIdentity',
            key: 'cooperateIdentity',
        },
        {
            title: '姓名',
            dataIndex: 'name',
            key: 'name',
        },
        {
            title: '手机号',
            dataIndex: 'mobile',
            key: 'mobile',
        },
        {
            title: '微信号',
            dataIndex: 'wechatNumber',
            key: 'wechatNumber',
        },
        {
            title: '拥有资源',
            dataIndex: 'haveResources',
            key: 'haveResources',
        },
        {
            title: '需求资源',
            dataIndex: 'demandResources',
            key: 'demandResources',
        },
        {
            title: '当前状态',
            // key: 'action',
            // render: (text, record) => (
            //     <div className="action-button-group">
            //       <p><Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showActionModal('add','修改应用',record)}}>修改</Button></p>
            //       <p><Button className="btn-delete" icon={<DeleteFilled />}>删除</Button></p>
            //       <p><Button className="btn-delete" icon={<BarChartOutlined />}>统计</Button></p>
            //     </div>
            // ),
        },
    ]; 
    const [dataList,setDataList] = useState([]);
    const [appListLoading,setAppListLoading] = useState(false);
    const [isShowModalVisible,setIsShowModalVisible] = useState(false);
    const [modalTitle,setModalTitle] = useState('');
    const [rowRecord,setRowRecord] = useState({});
    const [pagination,setPagination] = useState({
        current: 1,
        pageSize: 10
    });    
    const getAppList = () => {
        setAppListLoading(true);
        axiosPost('/manage/wx/gzh/resoDocking/list',{
            current: pagination.current,
            pageSize: pagination.pageSize,
            searchParams: ''
        }).then((res) => {
            if(res.code == 0){
                setAppListLoading(false);
                setDataList(res.data.list);
                setPagination({
                    current: res.data.pageNum,
                    pageSize: res.data.pageSize,
                    total: res.data.total                
                })
            }            
        }).catch((error) => {
            console.log(error);
        });
    }
    const handleTableChange = (page) => {
        setPagination({
            ...page
        })
    }
    useEffect(() => {
        getAppList();

        return () => {
            
        }
    },[pagination.current]);
    const showActionModal = (type,title,record) => {
        setIsShowModalVisible(true);
        setModalTitle(title);
        setRowRecord(record);
    }
    const hideModal = () => {
        setIsShowModalVisible(false);
    }
    return (
        <div className="application-box">
            <PageHeader title="H5商务对接" icon={AndroidFilled} routes={breadcrumbRoutes} />
            <div className="page-body">
                <div className="panel">
                    <div className="panel-title">商务对接列表</div>
                    <div className="panel-body">
                        {/* <div className="action">
                            <Button className="btn-add" icon={<PlusOutlined />} onClick={()=>{showActionModal('add','新增应用')}}>添加应用</Button>
                        </div> */}
                        <Table
                          bordered
                          loading={appListLoading}
                          dataSource={dataList}
                          columns={columns}
                          rowKey={(row) => {
                            return row.id
                          }}
                          pagination={{
                              current: pagination.current,
                              pageSize: pagination.pageSize,
                              total: pagination.total
                          }}
                          onChange={handleTableChange}
                        />
                    </div>
                </div>
            </div>
            <ActionModal visible={isShowModalVisible} title={modalTitle} record={rowRecord} hideModal={hideModal} refresh={getAppList} />
        </div>
    )
}
const mapStateToProps = (state) => ({username: state.LoginReducer.username})
export default connect(mapStateToProps)(Application)