import React, { useEffect, useState } from 'react';
import PageHeader from '../../component/PageHeader';
import { Row, Col } from 'antd';
import { axiosGet} from '@/extend/axios/index.js'
import './css/style.scss';
import {
    HomeFilled
} from '@ant-design/icons';

export default function Workbench(){
    const [todayNum,setTodayNum] = useState(0);
    const [weekNum,setWeekNum] = useState(0);
    const [monthNum,setMonthNum] = useState(0);

    const getData = () => {
        axiosGet('/manage/wx/gzh/resoDocking/statistics').then((res) => {
            if (res && res.code == 0) {
                setTodayNum(res.data.todayNum);
                setWeekNum(res.data.weekNum);
                setMonthNum(res.data.monthNum);
            }
        }).catch((error) => {
            console.log(error);
        });
    }
    useEffect(() => {
        getData();
    },[])
    return (
        <div className="workbench-box">
            <PageHeader title="数据统计" icon={HomeFilled} />
            <div className="content">
                <Row>
                    <Col span={8}>
                        <div className="item-card">
                            <div className="item-card-body bg01">
                                <img src={require('../../assets/images/circle.svg')} className="item-card-img" alt="" />
                                <h4>今日商务对接提交次数</h4>
                                <p><strong>{todayNum}</strong></p>
                            </div>
                        </div>                            
                    </Col>
                    <Col span={8}>
                        <div className="item-card">
                            <div className="item-card-body bg02">
                                <img src={require('../../assets/images/circle.svg')} className="item-card-img" alt="" />
                                <h4>本周商务对接提交次数</h4>
                                <p><strong>{weekNum}</strong></p>
                            </div>
                        </div>                            
                    </Col>
                    <Col span={8}>
                        <div className="item-card">
                            <div className="item-card-body bg03">
                                <img src={require('../../assets/images/circle.svg')} className="item-card-img" alt="" />
                                <h4>本月商务对接提交次数</h4>
                                <p><strong>{monthNum}</strong></p>
                            </div>
                        </div>                            
                    </Col>
                </Row>
            </div>
        </div>
    )
}