import React,{ useState, useEffect} from 'react'
import { Layout, Dropdown, Menu } from 'antd'
import { 
    DownOutlined,
    EditFilled,
    ExportOutlined,
} from '@ant-design/icons'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { axiosPost, axiosGet } from '@/extend/axios/index.js'
import { GET_MENU_LIST } from '@/store/actionTypes'
import ModifyInfo from './ModifyInfo'
import './css/style.scss'
const { Header } = Layout

function HeaderCustom(props){
    const history = useHistory();
    const [menuList,setMenuList] = useState([]);
    const [currentMenuId,setCurrentMenuId] = useState('');
    const getData = () => {
        axiosGet('/manage/sys/index').then((res) => {
            if(res && res.code == 0){
                console.log('调用aaa')        
                setMenuList(res.data)
                res.data.forEach(item => {
                    item.list.forEach(subItem => {
                        if(history.location.pathname === subItem.url) {
                            getSubMenu(item.list)
                            setCurrentMenuId(item.id.toString())
                        }
                    })
                })
                
            }
        }).catch((error) => {
            console.log(error);
        });
    }
    const getSubMenu = (list) => {
        props.getMenuList(list);
    }
    const selectedMenuItem = ({key}) => {
        const list = getListById(key)
        setCurrentMenuId(key)        
        getSubMenu(list)
        history.push(list[0].url)
    }
    const getListById = (id) => {
        for(let item of menuList){
            if(id == item.id) return item.list
        }
    }
    const modifyPersonalInfo = () => {
        setIsModifyInfoModalVisible(true);
    }
    const logOut = () =>{
        axiosPost('/manage/sys/logout').then((res) => {
            if(res && res.code == 0){
                localStorage.clear()                
                history.push('/login')
            }               
        }).catch((error) => {
            console.log(error);
        });        
    }
    const menu = (
        <Menu>
            <Menu.Item>
                <EditFilled /><span onClick={modifyPersonalInfo}>修改信息</span>
            </Menu.Item>
            <Menu.Divider />
            <Menu.Item>
                <ExportOutlined /><span onClick={logOut}>退出登录</span>
            </Menu.Item>
        </Menu>
    );
    const [isModifyInfoModalVisible,setIsModifyInfoModalVisible] = useState(false);
    const hideModal = () => {
        setIsModifyInfoModalVisible(false);
    }
    useEffect(() => {
        getData();
    },[])
    return (
        <div>
            <Header>
                <div className="logo">聚星阁传媒</div>
                <Menu theme="dark" mode="horizontal" selectedKeys={[currentMenuId]} onSelect={selectedMenuItem} className="main-menu">
                    { menuList.map((item,key) => {
                        return (
                            <Menu.Item key={item.id.toString()}>{item.name}</Menu.Item>
                        )
                    })} 
                </Menu>                             
                <Dropdown overlay={menu} placement="bottomCenter" trigger={['click']}>
                    <a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                        {props.username} <DownOutlined />
                    </a>
                </Dropdown>
            </Header>
            <ModifyInfo visible={isModifyInfoModalVisible} hideModal={hideModal} />
        </div>
    )
}
const mapStateToProps = (state) => {
    return {
        username: state.LoginReducer.username,
        //menuList: state.GlobalReducer.menuList
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        getMenuList(list){
            dispatch({
                type: GET_MENU_LIST,
                data: list
            })
        }
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(HeaderCustom)