import React,{ useEffect, useState, useRef } from 'react'
import { Form, Input, Button,message, Spin, Row, Col} from 'antd'
import { axiosPost } from '@/extend/axios/index.js'
import { connect } from 'react-redux'
import { SAVE_USER_INFO } from '@/store/actionTypes'
import { LoadingOutlined } from '@ant-design/icons'
import './css/style.scss'
const FormItem = Form.Item

function Login(props){
    console.log(props);
    const [form] = Form.useForm();
    const [isLoginLoading,setIsLoginLoading] = useState(false);
    const [isImgCodeLoading,setIsImgCodeLoading] = useState(false);
    const [imgCode,setImgCode] = useState('');
    const imgCodeRef = useRef();

    //验证手机号
    const checkMobile = (rule, value) =>{
        if (value && !/^1[3,4,5,6,7,8,9]\d{9}$/i.test(value)) {
            return Promise.reject('请输入正确的手机号');
        } else {
            return Promise.resolve();
        }
    }
    const onFinish = (values) => {
        setIsLoginLoading(true);
        axiosPost('/manage/sys/login',values).then((res) => {                   
            setIsLoginLoading(false);
            if(res.code == 0){                            
                localStorage.setItem('$Token',res.data.token);
                localStorage.setItem('username',values.username);
                props.saveUserInfo();
                message.success('登录成功',() => {
                    props.history.replace('/');
                });                
            }else{
                message.error(res.message,function(){
                    getImgCode();
                });                
            }          
        }).catch((error) => {
            console.log(error);
        });
    }
    const getImgCode = () => {
        setIsImgCodeLoading(true);
        setImgCode('/manage/sys/captcha?t=' + new Date().getTime());
        imgCodeRef.current.onload = () => {
            setIsImgCodeLoading(false);
        }
    }
    useEffect(() => {
        if(localStorage.getItem('$Token')){
            props.history.push('/');
        }
        getImgCode();
    },[]);
    return (
        <div className="login-box">
            <div className="main">
                <div className="main-form">
                    <Spin tip="登录中..." spinning={isLoginLoading}>
                        <h2 className="title">登录</h2>
                        <Form form={form} onFinish={onFinish}>
                            <FormItem name="username" rules={[
                                { required: true, message: '请输入账号'}
                            ]}>
                                <Input placeholder="请输入账号" />
                            </FormItem>
                            <FormItem name="password" rules={[{ required: true, message: '请输入密码'}]}>
                                <Input type="password" placeholder="请输入密码" />
                            </FormItem>
                            <FormItem>
                                <Row>
                                    <Col span={15}>
                                        <FormItem noStyle name="captcha" className="code-con" rules={[{ required: true, message: '请输入图形验证码'}]}>
                                            <Input placeholder="请输入图形验证码" />
                                        </FormItem>
                                    </Col>
                                    <Col span={9}>
                                        <Spin indicator={<LoadingOutlined />} spinning={isImgCodeLoading}>
                                            <span className="img-code" onClick={getImgCode}>
                                                <img ref={imgCodeRef} src={imgCode}></img>
                                            </span>
                                        </Spin>
                                    </Col>
                                </Row>                             
                            </FormItem>
                            <Form.Item>
                                <Button type="primary" htmlType="submit">登录</Button>                            
                            </Form.Item>                        
                        </Form>
                    </Spin>
                </div>
            </div>
        </div>
    )
}
const mapDispatchToProps = (dispatch) => {
    return {
        saveUserInfo(){
            dispatch({
                type: SAVE_USER_INFO,
                data: localStorage.getItem('username')
            });
        }
    }
}
export default connect(null,mapDispatchToProps)(Login);