import React, { useEffect } from 'react'
import { Modal, Form, Input, message } from 'antd'
import { axiosPost } from '@/extend/axios/index.js';
import '@/assets/css/modal.scss'
const FormItem = Form.Item;

export default function ActionModal(props){
    const {title,visible,record} = props;
    const [form] = Form.useForm();
    const formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 16 },
    };
    const handleOk = () => {
        form.resetFields();
        props.hideModal();
    }
    const handleCancel = () => {
        form.resetFields();
        props.hideModal();
    }
    useEffect(() => {
        form.setFieldsValue(record);
    },[record]);
    return (
        <div>
            <Modal
            getContainer={false}
            width={500}
            height={600}
            className="modal-common"
            title={title}
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            cancelText="关闭"
            >
                <div className="modal-inner-connent">
                    <Form {...formItemLayout} form={form}>
                        <FormItem name="id" noStyle={true}>
                            <Input type="hidden" />
                        </FormItem>
                        <FormItem label="公司" name="companyName" >
                          <Input type = "text" disabled  value={record.companyName}/>
                        </FormItem>
                        <FormItem label="地址" name="address" >
                          <Input type = "text" disabled value={record.address}/>
                        </FormItem>
                        <FormItem label="拥有资源" >
                          <Input type = "text" disabled value={record.hasResources}/>
                        </FormItem>
                        <FormItem label="需求资源"  >
                          <Input type = "text" disabled value={record.needResources}/>                        </FormItem>
                        <FormItem label="标签"  >
                          <Input type = "text" disabled value={record.lable}/>
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        </div>
    )
}