import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import PageHeader from '@/component/PageHeader'
import ActionModal from './component/ActionModal'
import {
    AndroidFilled,
    EditFilled,
    DeleteFilled,
    BarChartOutlined,
    PlusOutlined,
} from '@ant-design/icons'
import { Table, Button,message } from 'antd'
import { axiosPost} from '@/extend/axios/index.js'
import '@/assets/css/common.scss'

function SystemUserManage(props){
    console.log(props);
    const breadcrumbRoutes = [
        {
          path: '/sys/sysusers',
          breadcrumbName: '系统用户管理',
        },
        {
          path: '',
          breadcrumbName: '系统管理',
        }
    ];
    const columns = [
        {
            title: '账号',
            dataIndex: 'username',
            key: 'username',
        },
        {
            title: '邮箱',
            dataIndex: 'email',
            key: 'email',
        },
        {
            title: '手机号',
            dataIndex: 'mobile',
            key: 'mobile',
        },
        {
            title: '角色',
            dataIndex: 'roleName',
            key: 'roleName',
        },
        {
            title: '状态',
            dataIndex: 'status',
            key: 'status',
            render: (text, record) => {
                return text === 0 ? '禁用' : '正常'
            }
        },        
        {
            title: '操作',
            key: 'action',
            render: (text, record) => (
                <div className="action-button-group">
                  <p><Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showActionModal('edit','编辑',record)}}>编辑</Button></p>
                </div>
            )
        },
    ]; 
    const [dataList,setDataList] = useState([]);
    const [listLoading,setListLoading] = useState(false);
    const [isShowModalVisible,setIsShowModalVisible] = useState(false);
    const [modalType,setModalType] = useState('') 
    const [modalTitle,setModalTitle] = useState('');
    const [rowRecord,setRowRecord] = useState({});
    const [pagination,setPagination] = useState({
        current: 1,
        pageSize: 10
    });
    const [roleList,setRoleList] = useState()  
    const getList = () => {
        setListLoading(true);
        axiosPost('/manage/sys/user/list',{
            current: pagination.current,
            pageSize: pagination.pageSize
        }).then((res) => {
            if(res.code == 0){
                setListLoading(false);
                setDataList(res.data.list);
                setPagination({
                    current: res.data.pageNum,
                    pageSize: res.data.pageSize,
                    total: res.data.total                
                })
            }            
        }).catch((error) => {
            console.log(error);
        });
    }
    const getRoleList = () => {
        axiosPost('/manage/sys/role/listrole').then((res) => {            
            if(res.code == 0){                
                setRoleList(res.data) 
            }else{
                message.error(res.message);
            }          
        }).catch((error) => {
            console.log(error);
        })
    }
    const handleTableChange = (page) => {
        setPagination({
            ...page
        })
    }
    useEffect(() => {
        getList()
        getRoleList()
        return () => {
            
        }
    },[pagination.current]);
    const showActionModal = (type,title,record) => {
        setIsShowModalVisible(true)
        setModalTitle(title)
        setModalType(type)
        if (type === 'edit') setRowRecord(record);
    }
    const hideModal = () => {
        setIsShowModalVisible(false);
    }
    return (
        <div className="application-box">
            <PageHeader title="系统用户管理" icon={AndroidFilled} routes={breadcrumbRoutes} />
            <div className="page-body">
                <div className="panel">
                    <div className="panel-title">系统用户管理</div>
                    <div className="panel-body">
                        <div className="action">
                            <Button className="btn-add" icon={<PlusOutlined />} onClick={()=>{showActionModal('add','新增')}}>新增用户</Button>
                        </div>
                        <Table
                          bordered
                          loading={listLoading}
                          dataSource={dataList}
                          columns={columns}
                          rowKey={(row) => {
                            return row.id
                          }}
                          pagination={{
                              current: pagination.current,
                              pageSize: pagination.pageSize,
                              total: pagination.total
                          }}
                          onChange={handleTableChange}
                        />
                    </div>
                </div>
            </div>
            <ActionModal visible={isShowModalVisible} title={modalTitle} roleList={roleList} record={rowRecord} type={modalType} hideModal={hideModal} refresh={getList} />
        </div>
    )
}
export default SystemUserManage