import React, { useEffect, useState } from 'react'
import { Modal, Form, Input, message} from 'antd'
import { axiosPost } from '@/extend/axios/index.js';
import '@/assets/css/modal.scss'
const FormItem = Form.Item;

export default function ActionModal(props){
    const {type,title,visible,record} = props;
    const [form] = Form.useForm();    
    const formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 16 },
    };
    const handleOk = () => {
        form.validateFields().then(values => {
            console.log(values);
            const params = {
                role: JSON.stringify(values)
            }
            params.state = type === 'add' ? 'create' : 'update'
            axiosPost('/manage/sys/role/save',params).then((res) => {            
                if(res.code == 0){                
                    message.success("保存成功");
                    handleCancel();
                    props.refresh();
                }else{
                    message.error(res.message);
                }          
            }).catch((error) => {
                console.log(error);
            });
        }).catch(error => {
            console.log(error);
        })
    }
    const handleCancel = () => {
        form.resetFields();
        props.hideModal();
    }
    useEffect(() => {
        form.setFieldsValue(record)
    },[record]);
    return (
        <div>
            <Modal
            getContainer={false}
            width={600}
            className="modal-common"
            title={title}
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            >
                <div className="modal-inner-connent">
                    <Form {...formItemLayout} form={form}>
                        <FormItem name="id" noStyle={true}>
                            <Input type="hidden" />
                        </FormItem>
                        <FormItem label="角色名称" name="name" rules={[{ required: true, message: '请输入角色名称'}]}>
                            <Input placeholder="请输入角色名称" />
                        </FormItem>
                        <FormItem label="角色唯一标识" name="nid" rules={[{ required: true, message: '请输入角色唯一标识'}]}>
                            <Input placeholder="请输入角色唯一标识" />
                        </FormItem>
                        <FormItem label="备注" name="remark" rules={[{ required: true, message: '请输入备注'}]}>
                            <Input.TextArea placeholder="请输入备注" />
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        </div>
    )
}