import React, { useEffect, useState } from 'react'
import { Select } from 'antd';
import { Modal, Form, Input, message } from 'antd'
import { axiosPost } from '@/extend/axios/index.js';
import '@/assets/css/modal.scss'
const FormItem = Form.Item;

export default function AuditActionModal(props){
    const {title,visible,record} = props;
    const [form] = Form.useForm();
    const { Option } = Select;
    const formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 16 },
    };
    const handleOk = () => {
        form.validateFields().then(values => {
            console.log(values);
            if(values.state ==null){
                alert("请先选择")
            }
            axiosPost('/manage/wx/minipro/userNeed/check/',{
                id:record.id,
                state:values.state
            }).then((res) => {
                if(res.code == '0'){
                    message.success("操作成功");
                    handleCancel();
                    props.refresh();
                }else{
                    message.error(res.message);
                }
            }).catch((error) => {
                console.log(error);
            });
        }).catch(error => {
            console.log(error);
        })
    }
    const handleCancel = () => {
        form.resetFields();
        props.hideModal();
    }
    useEffect(() => {
        form.setFieldsValue(record)
    },[record]);
    return (
        <div>
            <Modal
            getContainer={false}
            width={500}
            className="modal-common"
            title={title}
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            >
                <div className="modal-inner-connent">
                    <Form {...formItemLayout} form={form}>
                        <FormItem name="id" noStyle={true}>
                            <Input type="hidden" />
                        </FormItem>
                        <FormItem label="审核" name="state">
                            <Select>
                                <Option value={30}>上架</Option>
                                <Option value={31}>下架</Option>
                            </Select>
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        </div>
    )
}