import { GET_MENU_LIST } from '@/store/actionTypes'
let defaultState = {
    menuList: []
}
export default (state = defaultState,action) => {
    console.log(action);
    switch (action.type){
        case GET_MENU_LIST:
            return Object.assign({},state,{
                menuList: [...action.data]
            })
        default:
            return state;
    }
}