import React, { useEffect, useState } from 'react'
import { Modal, Form, Input, message,Select, InputNumber } from 'antd'
import { axiosPost } from '@/extend/axios/index.js';
import '@/assets/css/modal.scss'
const FormItem = Form.Item;
const { Option } = Select

export default function ActionModal(props){
    const {type,title,visible,record} = props;
    const [form] = Form.useForm();    
    const formItemLayout = {
        labelCol: { span: 7 },
        wrapperCol: { span: 15 },
    };
    const handleOk = () => {
        form.validateFields().then(values => {
            console.log(values);
            const url = type === 'add' ? '/manage/wx/minipro/userConfig/save' : '/manage/wx/minipro/userConfig/update'
            axiosPost(url,values).then((res) => {            
                if(res.code == 0){                
                    message.success("保存成功");
                    handleCancel();
                    props.refresh();
                }else{
                    message.error(res.message);
                }          
            }).catch((error) => {
                console.log(error);
            });
        }).catch(error => {
            console.log(error);
        })
    }
    const handleCancel = () => {
        form.resetFields();
        props.hideModal();
    }
    useEffect(() => {
        type === 'update' && form.setFieldsValue(record)
    },[record]);
    return (
        <div>
            <Modal
            getContainer={false}
            width={600}
            className="modal-common"
            title={title}
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            >
                <div className="modal-inner-connent">
                    <Form {...formItemLayout} form={form}>
                        {
                            type === 'update' && 
                            <FormItem name="id" noStyle={true}>
                                <Input type="hidden" />
                            </FormItem>
                        }
                        <FormItem label="分享次数" name="shareNum" rules={[{required: true, message: '请输入分享次数'}]}>
                            <InputNumber placeholder="请输入分享次数" />
                        </FormItem>
                        <FormItem label="使用积分" name="contactIntegral" rules={[{ required: true, message: '请输入使用积分'}]}>
                            <InputNumber placeholder="请输入使用积分" />
                        </FormItem>
                        <FormItem label="开通会员费用" name="memberAmount" rules={[{ required: true, message: '请输入开通会员费用'}]}>
                            <InputNumber placeholder="请输入开通会员费用" />
                        </FormItem>
                        <FormItem label="付费查看" name="payAmount" rules={[{ required: true, message: '请输入付费查看金额'}]}>
                            <InputNumber placeholder="请输入付费查看金额" />
                        </FormItem>
                        <FormItem label="完成后可再次查看次数" name="lookNum" rules={[{ required: true, message: '请输入完成后可再次查看次数'}]}>
                            <InputNumber placeholder="请输入完成后可再次查看次数" />
                        </FormItem>
                        <FormItem label="推荐位消耗积分" name="recommenIntegral" rules={[{ required: true, message: '请输入推荐位消耗积分'}]}>
                            <InputNumber placeholder="请输入推荐位消耗积分" />
                        </FormItem>
                        <FormItem label="签到积分" name="signIntegral" rules={[{ required: true, message: '请输入签到积分'}]}>
                            <InputNumber placeholder="请输入签到积分" />
                        </FormItem>
                        <FormItem label="积分规则文案" name="integralRule" rules={[{ required: true, message: '请输入积分规则文案'}]}>
                            <Input.TextArea placeholder="请输入积分规则文案" />
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        </div>
    )
}