import React, { useEffect } from 'react'
import { Modal, Form, Input, message } from 'antd'
import { axiosPost } from '@/extend/axios/index.js';
import '@/assets/css/modal.scss'
const FormItem = Form.Item;

export default function ActionModal(props){
    const {title,visible,record} = props;
    const [form] = Form.useForm();
    const formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 16 },
    };
    //验证手机号
    const checkMobile = (rule, value) =>{
        if (value && !/^1[3,4,5,6,7,8,9]\d{9}$/i.test(value)) {
            return Promise.reject('请输入正确的手机号');
        } else {
            return Promise.resolve();
        }
    }
    const handleOk = () => {
        form.validateFields().then(values => {
            console.log(values);
            axiosPost('/api/v1/app/update',values).then((res) => {            
                if(res.code === 0){                
                    message.success("修改成功");
                    handleCancel();
                    props.refresh();
                }else{
                    message.error(res.message);
                }          
            }).catch((error) => {
                console.log(error);
            });
        }).catch(error => {
            console.log(error);
        })
    }
    const handleCancel = () => {
        form.resetFields();
        props.hideModal();
    }
    useEffect(() => {
        console.log('ddd');
        form.setFieldsValue(record);
    },[record]);
    return (
        <div>
            <Modal
            getContainer={false}
            width={800}
            className="modal-common"
            title={title}
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            >
                <div className="modal-inner-connent">
                    <Form {...formItemLayout} form={form}>
                        {/* <Input name="id" type="hidden" /> */}
                        <FormItem name="id" noStyle={true}>
                            <Input type="hidden" />
                        </FormItem>
                        {/* <FormItem label="手机号" name="mobile" rules={[
                            { required: true, message: '请输入手机号'},
                            { validator: checkMobile}
                        ]}>
                            <Input placeholder="请输入手机号" />
                        </FormItem> */}
                        <FormItem label="应用名称" name="appName" rules={[{ required: true, message: '请输入应用名称'}]}>
                            <Input placeholder="请输入应用名称" />
                        </FormItem>
                        <FormItem label="黑名单命中跳转链接" name="hitBlacklistJumpLink" rules={[{ required: true, message: '请输入黑名单命中跳转链接'}]}>
                            <Input placeholder="请输入黑名单命中跳转链接" />
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        </div>
    )
}