import React, { useEffect, useState } from 'react'
import { Modal, message, Tree, Button} from 'antd'
import '@/assets/css/modal.scss'
import '../css/style.scss'
import axios from 'axios'

export default function AuthorityModal(props){
    const {visible,authorityList,checkedKeysList,record} = props;
    const [checkedKeys, setCheckedKeys] = useState([])
    const [expandedKeys, setExpandedKeys] = useState([])
    const handleCancel = () => {
        selectedNone()
        expandNone()
        props.hideModal();
    }
    const handleOk = () => {
        axios.post('/manage/sys/menu/updatemenu',{
            menus: checkedKeys,
            roleId: record.id
        }).then((res) => {            
            if(res.data.code == 0){    
                message.success("权限修改成功")
                handleCancel()
            }else{
                message.error(res.message);
            }          
        }).catch((error) => {
            console.log(error);
        })
    }
    const selectedAll = () => {
        setCheckedKeys(getCheckedKeys(authorityList))
    }
    const selectedNone = () => {
        setCheckedKeys([])
    }
    const expandAll = () => {
        setExpandedKeys(getCheckedKeys(authorityList))
    }
    const expandNone = () => {
        setExpandedKeys([])
    }
    const onExpand = (expandedKeys) => {
        setExpandedKeys(expandedKeys)
    } 
    const getCheckedKeys = (tree = []) => {
        const list = []
        if(!!tree && tree.length !== 0) {
            authorityList.forEach((item) => {
                list.push(item.key)
            })
            return list
        }
    }
    const onCheck = checkedKeys => {
        setCheckedKeys(checkedKeys);
    };
    useEffect(() => {
        //!!checkedKeysList && setLoading(false)
        setCheckedKeys(checkedKeysList)           
    },[checkedKeysList]);
    return (
        <div>
            <Modal
            getContainer={false}
            width={800}
            className="modal-common"
            title="分配权限"
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            >
                <div className="modal-inner-connent">
                    <div className="action-group">
                        <Button onClick={selectedAll}>全选</Button>
                        <Button onClick={selectedNone}>全不选</Button>
                        <Button onClick={expandAll}>展开所有</Button>
                        <Button onClick={expandNone}>收缩所有</Button>
                    </div>
                    <Tree 
                      checkable
                      checkedKeys={checkedKeys}
                      expandedKeys={expandedKeys}
                      treeData={authorityList}
                      onCheck={onCheck}
                      onExpand={onExpand}              
                    />                                      
                </div>
            </Modal>
        </div>
    )
}