import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import PageHeader from '@/component/PageHeader'
import ActionModal from './component/ActionModal'
import AuthorityModal from './component/AuthorityModal'
import {
    AndroidFilled,
    EditFilled,
    DeleteFilled,
    BarChartOutlined,
    PlusOutlined,
} from '@ant-design/icons'
import { Table, Button,message, Spin} from 'antd'
import { axiosPost} from '@/extend/axios/index.js'
import '@/assets/css/common.scss'

function SystemRoleManage(props){
    const breadcrumbRoutes = [
        {
          path: '/sys/sysroles',
          breadcrumbName: '系统角色管理',
        },
        {
          path: '',
          breadcrumbName: '系统管理',
        }
    ];
    const columns = [
        {
            title: '角色名称',
            dataIndex: 'name',
            key: 'name',
        },
        {
            title: '角色唯一标识',
            dataIndex: 'nid',
            key: 'nid',
        },
        {
            title: '创建时间',
            dataIndex: 'createTime',
            key: 'createTime',
            render: (text,record) => {
                const timeArr = text.split('T')

                return timeArr[0] + ' ' + timeArr[1].split('.')[0]
            }
        },
        {
            title: '修改时间',
            dataIndex: 'updateTime',
            key: 'updateTime',
            render: (text,record) => {
                const timeArr = text.split('T')

                return timeArr[0] + ' ' + timeArr[1].split('.')[0]
            }
        },
        {
            title: '创建人',
            dataIndex: 'username',
            key: 'username',
        },
        {
            title: '备注',
            dataIndex: 'remark',
            key: 'remark',
        },    
        {
            title: '操作',
            key: 'action',
            render: (text, record) => (
                <div className="action-button-group">
                  <p><Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showActionModal('edit','编辑',record)}}>编辑</Button></p>
                  <p><Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showAuthorityModal(record)}}>分配权限</Button></p>
                </div>
            ),
        },
    ]; 
    const [dataList,setDataList] = useState([]);
    const [appListLoading,setAppListLoading] = useState(false)
    const [modalLoading,setModalLoading] = useState(false);
    const [isShowModalVisible,setIsShowModalVisible] = useState(false);
    const [isShowAuthorityModalVisible,setIsShowAuthorityModalVisible] = useState(false)
    const [modalType,setModalType] = useState('') 
    const [modalTitle,setModalTitle] = useState('');
    const [rowRecord,setRowRecord] = useState({})
    const [authorityList,setAuthorityList] = useState([])
    const [checkedKeysList, setCheckedKeysList] = useState([])
    const [pagination,setPagination] = useState({
        current: 1,
        pageSize: 10
    });    
    const getList = () => {
        setAppListLoading(true);
        axiosPost('/manage/sys/role/list',{
            current: pagination.current,
            pageSize: pagination.pageSize
        }).then((res) => {
            if(res.code == 0){
                setAppListLoading(false);
                setDataList(res.data.list);
                setPagination({
                    current: res.data.pageNum,
                    pageSize: res.data.pageSize,
                    total: res.data.total                
                })
            }            
        }).catch((error) => {
            console.log(error);
        });
    }
    const handleTableChange = (page) => {
        setPagination({
            ...page
        })
    }
    useEffect(() => {
        getList();

        return () => {
            
        }
    },[pagination.current]);
    const showActionModal = (type,title,record) => {
        setIsShowModalVisible(true);
        setModalTitle(title);
        setRowRecord(record);
        setModalType(type)
    }
    const checkedList = []
    const getTree = (tree = []) =>{
        const newTreelist = []        
        if(tree && tree.length !== 0){                                  
            tree.forEach((currentValue,index) => {
                const item = {}
                item.title = currentValue.label
                item.key = currentValue.value
                item.checked = currentValue.checked                               
                if(currentValue.expanded) item.children = getTree(currentValue.children)
                item.checked && checkedList.push(currentValue.value)                
                newTreelist.push(item)                
            })            
        }
        return newTreelist
    }
    const showAuthorityModal = (record) => {
        setModalLoading(true)     
        axiosPost('/manage/sys/menu/fetch',{
            roleId: record.id
        }).then((res) => {            
            if(res.code == 0){
                setModalLoading(false)    
                const tree = getTree(res.data)
                setAuthorityList(tree)
                console.log(checkedList)
                setCheckedKeysList(checkedList)
                setIsShowAuthorityModalVisible(true)
                setRowRecord(record)
            }else{
                message.error(res.message);
            }          
        }).catch((error) => {
            console.log(error);
        })
    }
    const hideModal = () => {
        setIsShowModalVisible(false)
        setIsShowAuthorityModalVisible(false)
    }
    return (
        <div className="application-box">
            <PageHeader title="系统角色管理" icon={AndroidFilled} routes={breadcrumbRoutes} />
            <div className="page-body">
                <div className="panel">
                    <div className="panel-title">系统角色管理</div>
                    <div className="panel-body">
                        <Spin spinning={modalLoading}>
                            <div className="action">
                                <Button className="btn-add" icon={<PlusOutlined />} onClick={()=>{showActionModal('add','新增')}}>新增角色</Button>
                            </div>
                            <Table
                            bordered
                            loading={appListLoading}
                            dataSource={dataList}
                            columns={columns}
                            rowKey={(row) => {
                                return row.id
                            }}
                            pagination={{
                                current: pagination.current,
                                pageSize: pagination.pageSize,
                                total: pagination.total
                            }}
                            onChange={handleTableChange}
                            />
                        </Spin>                        
                    </div>
                </div>
            </div>
            <ActionModal visible={isShowModalVisible} title={modalTitle} record={rowRecord} type={modalType} hideModal={hideModal} refresh={getList} />
            <AuthorityModal visible={isShowAuthorityModalVisible} authorityList={authorityList} record={rowRecord} checkedKeysList={checkedKeysList} hideModal={hideModal} refresh={getList} />
        </div>
    )
}
export default SystemRoleManage