import React, { useState,useEffect } from 'react'
import { Layout, Menu} from 'antd'
import {
    HomeFilled,    
    AndroidFilled,
    ProfileFilled,
} from '@ant-design/icons'
import { Link, useHistory} from "react-router-dom"
import { connect } from 'react-redux'
import { axiosGet} from '@/extend/axios/index.js'
import style from './css/style.module.scss'
const { Sider } = Layout

function SideBar(props){
    let history = useHistory();
    let [isCollapsed,setIsCollapsed] = useState(false)
    let [selectedMenu,setSelectedMenu] = useState('')

    const onCollapse = collapsed => {
        setIsCollapsed(collapsed);
    }
    const selectedMenuItem = ({key}) => {
        setSelectedMenu(key)
    }
    useEffect(() => {
        setSelectedMenu(history.location.pathname)
    });    
    return (
        <Sider collapsible collapsed={isCollapsed} width={200} className={style['page-sider']} onCollapse={onCollapse}>
            <Menu
            mode="inline"
            selectedKeys={[selectedMenu]}
            onSelect={selectedMenuItem}
            >
                { props.menuList.map((item) => {                    
                    return (                        
                        <Menu.Item key={item.url}>
                            <Link to={item.url}>{item.name}</Link>
                        </Menu.Item>
                    )
                })}
                {/* <Menu.Item key="/workbench">
                    <HomeFilled />
                    <Link to="/workbench">数据统计</Link>
                </Menu.Item>
                <Menu.Item key="/application">
                    <AndroidFilled />
                    <Link to="/application" title="H5商务对接">H5商务对接</Link>
                </Menu.Item> */}
                {/* <Menu.Item key="/blacklist">
                    <ProfileFilled />
                    <Link to="/blacklist">来源黑名单</Link>
                </Menu.Item> */}
            </Menu>
        </Sider>
    )
}
const mapStateToProps = (state) => ({menuList: state.GlobalReducer.menuList})
export default connect(mapStateToProps,null)(SideBar)