import { SAVE_USER_INFO, TRIGGER_EXPIRED } from '@/store/actionTypes'
let defaultState = {
    username: localStorage.getItem('username'),
    expired: 0
}
export default (state = defaultState,action) => {
    switch (action.type){
        case SAVE_USER_INFO:
            return Object.assign({},state,{
                username: action.data
            })
        case TRIGGER_EXPIRED:
            return Object.assign({},state,{
                expired: state.expired+1
            })
        default:
            return state;
    }
}