import React, {useEffect, useState} from 'react'
import PageHeader from '../../component/PageHeader'
import {
    AndroidFilled,
    EditFilled,
    DeleteFilled,
    BarChartOutlined,
    PlusOutlined,
} from '@ant-design/icons'
import { Table, Button } from 'antd'
import { axiosPost} from '@/extend/axios/index.js'
import { connect } from 'react-redux'
import '@/assets/css/common.scss'

function Channel(props){
    const { location } = props;
    const breadcrumbRoutes = [
        {
          path: '/application',
          breadcrumbName: '渠道',
        },
        {
          path: '',
          breadcrumbName: '渠道列表',
        }
    ];
    const columns = [
        {
            title: 'ID',
            dataIndex: 'id',
            key: 'id',
        },
        {
            title: '应用名称',
            dataIndex: 'appName',
            key: 'appName',
        },
        {
            title: '应用编号',
            dataIndex: 'appNo',
            key: 'appNo',
        },
        {
            title: '图标',
            dataIndex: 'icon',
            key: 'icon',
            render: (text, record) => (
                <img src={text} alt="" className="td-img" />
            ),
        },
        {
            title: '黑名单命中跳转链接',
            dataIndex: 'hitBlacklistJumpLink',
            key: 'hitBlacklistJumpLink',
        },
        {
            title: '基本操作',
            key: 'basicAction',
            render: (text, record) => (
                <div>
                    <p><a>短链转化</a></p>
                    <p><a>非法访问拦截记录</a></p>
                    <p><a>拦截记录统计</a></p>
                    <p><a>白名单管理</a></p>
                </div>
            ),
        },
        {
            title: '创建时间',
            dataIndex: 'createTime',
            key: 'createTime',
        },
        {
            title: '操作',
            key: 'action',
            render: (text, record) => (
                <div className="action-button-group">
                  <p><Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showActionModal('add','修改应用',record)}}>修改</Button></p>
                  <p><Button className="btn-delete" icon={<DeleteFilled />}>删除</Button></p>
                  <p><Button className="btn-delete" icon={<BarChartOutlined />}>统计</Button></p>
                </div>
            ),
        },
    ]; 
    const [dataList,setDataList] = useState([]);
    const [channelListLoading,setChannelListLoading] = useState(true);
    const [isShowModalVisible,setIsShowModalVisible] = useState(false);
    const [modalTitle,setModalTitle] = useState('');
    const [rowRecord,setRowRecord] = useState({});
    useEffect(() => {
        getAppList();
    },[]);
    const getAppList = () => {
        axiosPost('/api/v1/channel/list',{
            "appId": location.state.appId,
            "channelName": "",
            "pageNo": 1
        }).then((res) => {
            setChannelListLoading(false);
            setDataList(res.data.list);
        }).catch((error) => {
            console.log(error);
        });
    }
    const showActionModal = (type,title,record) => {
        setIsShowModalVisible(true);
        setModalTitle(title);
        setRowRecord(record);
    }
    const hideModal = () => {
        setIsShowModalVisible(false);
    }
    return (
        <div className="application-box">
            <PageHeader title="渠道" icon={AndroidFilled} routes={breadcrumbRoutes} />
            <div className="page-body">
                <div className="panel">
                    <div className="panel-title">渠道列表</div>
                    <div className="panel-body">
                        {/* <div className="action">
                            <Button className="btn-add" icon={<PlusOutlined />} onClick={()=>{showActionModal('add','新增应用')}}>添加应用</Button>
                        </div> */}
                        <Table
                          bordered
                          loading={channelListLoading}
                          dataSource={dataList}
                          columns={columns}
                          rowKey={(row) => {
                            return row.id
                          }}
                        />
                    </div>
                </div>
            </div>
        </div>
    )
}
const mapStateToProps = (state) => ({username: state.username})
export default connect(mapStateToProps)(Channel)