import React, { useEffect } from 'react'
import { Select } from 'antd';
import { Modal, Form, Input, message } from 'antd'
import { axiosPost } from '@/extend/axios/index.js';
import '@/assets/css/modal.scss'
const FormItem = Form.Item;

export default function AuditActionModal(props){
    const {title,visible,record} = props;
    const [form] = Form.useForm();
    const { Option } = Select;
    const constructor = ()=> {
        this.state = {
            value: ''
        }
        this.handleChange = this.handleChange.bind(this);
    }
    const formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 16 },
    };
    //验证手机号
    const checkMobile = (rule, value) =>{
        if (value && !/^1[3,4,5,6,7,8,9]\d{9}$/i.test(value)) {
            return Promise.reject('请输入正确的手机号');
        } else {
            return Promise.resolve();
        }
    }
    const handleOk = () => {
        form.validateFields().then(values => {
            console.log(values);
            axiosPost('/manage/wx/minipro/userNeed/refusereason',{
                id:values.id,
                refuseReason:values.refuseReason
            }).then((res) => {
                if(res.code == '0'){
                    message.success("操作成功");
                    handleCancel();
                    props.refresh();
                }else{
                    message.error(res.message);
                }
            }).catch((error) => {
                console.log(error);
            });
        }).catch(error => {
            console.log(error);
        })
    }
    const handleCancel = () => {
        form.resetFields();
        props.hideModal();
    }
    const handleChange =(e) => { // 选择
        console.log('selected ${value}')
    }
    useEffect(() => {
        form.setFieldsValue(record);
    },[record]);
    return (
        <div>
            <Modal
            getContainer={false}
            width={500}
            className="modal-common"
            title={title}
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            >
                <div className="modal-inner-connent">
                    <Form {...formItemLayout} form={form}>
                        {/* <Input name="id" type="hidden" /> */}
                        <FormItem name="id" noStyle={true}>
                        <Input type="hidden" />
                        </FormItem>
                        {/* <FormItem label="手机号" name="mobile" rules={[
                            { required: true, message: '请输入手机号'},
                            { validator: checkMobile}
                        ]}>
                            <Input placeholder="请输入手机号" />
                        </FormItem> */}
                        <FormItem label="拒绝理由" name="refuseReason" rules={[{ required: true, message: '请输入拒绝理由'}]}>
                            <Input placeholder="请输入拒绝理由" />
                        </FormItem>



                    </Form>
                </div>
            </Modal>
        </div>
    )
}