import React, { useEffect, useState } from 'react'
import { Modal, Form, Input, message,Select, Radio } from 'antd'
import { axiosPost } from '@/extend/axios/index.js';
import '@/assets/css/modal.scss'
const FormItem = Form.Item;
const { Option } = Select

export default function ActionModal(props){
    const {type,title,visible,record,roleList} = props;
    const [form] = Form.useForm();    
    const formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 16 },
    };
    //验证手机号
    const checkMobile = (rule, value) =>{
        if (value && !/^1[3,4,5,6,7,8,9]\d{9}$/i.test(value)) {
            return Promise.reject('请输入正确的手机号');
        } else {
            return Promise.resolve();
        }
    }
    const handleOk = () => {
        form.validateFields().then(values => {
            console.log(values);
            values.roleId = values.roleId.join(',')
            const params = {
                user: JSON.stringify(values)
            }
            params.state = type === 'add' ? 'create' : 'update'
            axiosPost('/manage/sys/user/save',params).then((res) => {            
                if(res.code == 0){                
                    message.success("保存成功");
                    handleCancel();
                    props.refresh();
                }else{
                    message.error(res.message);
                }          
            }).catch((error) => {
                console.log(error);
            });
        }).catch(error => {
            console.log(error);
        })
    }
    const handleCancel = () => {
        form.resetFields();
        props.hideModal();
    }
    useEffect(() => {
        type === 'edit' && form.setFieldsValue(record)
        form.setFieldsValue({
            roleId: [roleList ? roleList[0].id.toString() : ''],
            status: '1'
        })
    },[props]);
    return (
        <div>
            <Modal
            getContainer={false}
            width={600}
            className="modal-common"
            title={title}
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            >
                <div className="modal-inner-connent">
                    <Form {...formItemLayout} form={form}>
                        <FormItem name="id" noStyle={true}>
                            <Input type="hidden" />
                        </FormItem>
                        <FormItem label="账号" name="username" rules={[{ required: true, message: '请输入账号'}]}>
                            <Input placeholder="请输入账号" />
                        </FormItem>
                        <FormItem label="邮箱" name="email" rules={[{ type: 'email', required: true, message: '请输入邮箱'}]}>
                            <Input placeholder="请输入邮箱" />
                        </FormItem>
                        <FormItem label="手机号" name="mobile" rules={[
                            { required: true, message: '请输入手机号'},
                            { validator: checkMobile}
                        ]}>
                            <Input placeholder="请输入手机号" />
                        </FormItem>
                        <FormItem label="角色" name="roleId" rules={[{ required: true, message: '请选择角色'}]}>
                            <Select mode="multiple">
                                { roleList && roleList.map((item, key) => {
                                    return (
                                        <Option value={item.id.toString()} key={key}>{item.name}</Option>
                                    )
                                })}
                            </Select>
                        </FormItem>
                        <FormItem label="状态" name="status" rules={[{ required: true, message: '请选择状态'}]}>
                            <Radio.Group>
                                <Radio value="0">禁用</Radio>
                                <Radio value="1">正常</Radio>
                            </Radio.Group>
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        </div>
    )
}