import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import PageHeader from '@/component/PageHeader'
import ActionModal from './component/ActionModal'
import {
    AndroidFilled,
    EditFilled,
    DeleteFilled,
    BarChartOutlined,
    PlusOutlined,
} from '@ant-design/icons'
import { Table, Button,message } from 'antd'
import { axiosPost} from '@/extend/axios/index.js'
import '@/assets/css/common.scss'

function SystemUserManage(props){
    console.log(props);
    const breadcrumbRoutes = [
        {
          path: '/sys/paramconfiguration',
          breadcrumbName: '参数配置',
        },
        {
          path: '',
          breadcrumbName: '系统管理',
        }
    ];
    const columns = [
        {
            title: '分享次数',
            dataIndex: 'shareNum',
            key: 'shareNum',
        },
        {
            title: '使用积分',
            dataIndex: 'contactIntegral',
            key: 'contactIntegral',
        },
        {
            title: '开通会员费用',
            dataIndex: 'memberAmount',
            key: 'memberAmount',
        },
        {
            title: '付费查看',
            dataIndex: 'payAmount',
            key: 'payAmount',
        },        
        {
            title: '完成后可再次查看次数',
            dataIndex: 'lookNum',
            key: 'lookNum',
        },
        {
            title: '推荐位消耗积分',
            dataIndex: 'recommenIntegral',
            key: 'recommenIntegral',
        },
        {
            title: '签到积分',
            dataIndex: 'signIntegral',
            key: 'signIntegral',
        },
        {
            title: '积分规则文案',
            dataIndex: 'integralRule',
            key: 'integralRule',
        },       
        {
            title: '操作',
            key: 'action',
            render: (text, record) => (
                <div className="action-button-group">
                  <p><Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showActionModal('update','编辑',record)}}>编辑</Button></p>
                </div>
            )
        },
    ]; 
    const [dataList,setDataList] = useState([]);
    const [listLoading,setListLoading] = useState(false);
    const [isShowModalVisible,setIsShowModalVisible] = useState(false);
    const [modalType,setModalType] = useState('') 
    const [modalTitle,setModalTitle] = useState('');
    const [rowRecord,setRowRecord] = useState({});
    const [pagination,setPagination] = useState({
        current: 1,
        pageSize: 10
    })
    const [total,setTotal] = useState(1);
    const getList = () => {
        setListLoading(true);
        axiosPost('/manage/wx/minipro/userConfig/list',{
            current: pagination.current,
            pageSize: pagination.pageSize
        }).then((res) => {
            if(res.code == 0){
                setListLoading(false);
                setDataList(res.data.list);
                setPagination({
                    current: res.data.pageNum,
                    pageSize: res.data.pageSize,
                    total: res.data.total                
                })
                setTotal(res.data.total)
            }            
        }).catch((error) => {
            console.log(error);
        });
    }
    const handleTableChange = (page) => {
        setPagination({
            ...page
        })
    }
    useEffect(() => {
        getList()
        return () => {
            
        }
    },[pagination.current]);
    const showActionModal = (type,title,record) => {
        setIsShowModalVisible(true)
        setModalTitle(title)
        setModalType(type)
        if (type === 'update') setRowRecord(record);
    }
    const hideModal = () => {
        setIsShowModalVisible(false);
    }
    return (
        <div className="application-box">
            <PageHeader title="参数配置" icon={AndroidFilled} routes={breadcrumbRoutes} />
            <div className="page-body">
                <div className="panel">
                    <div className="panel-title">参数配置</div>
                    <div className="panel-body">
                        <div className="action">
                            <Button disabled={total === 0 ? false : true} className="btn-add" icon={<PlusOutlined />} onClick={()=>{showActionModal('add','新增')}}>新增配置</Button>
                        </div>
                        <Table
                          bordered
                          loading={listLoading}
                          dataSource={dataList}
                          columns={columns}
                          rowKey={(row) => {
                            return row.id
                          }}
                          pagination={{
                              current: pagination.current,
                              pageSize: pagination.pageSize,
                              total: pagination.total
                          }}
                          onChange={handleTableChange}
                        />
                    </div>
                </div>
            </div>
            <ActionModal visible={isShowModalVisible} title={modalTitle} record={rowRecord} type={modalType} hideModal={hideModal} refresh={getList} />
        </div>
    )
}
export default SystemUserManage