import React, {useEffect, useState} from 'react'
import { useHistory} from 'react-router-dom'
import PageHeader from '@/component/PageHeader'
import {
    AndroidFilled,
    EditFilled,
    DeleteFilled,
    BarChartOutlined,
    PlusOutlined,
    ExclamationCircleOutlined
} from '@ant-design/icons'
import {Table, Button, Modal, Tabs, message} from 'antd'
import { axiosPost} from '@/extend/axios/index.js'
import { connect } from 'react-redux'
import ActionModal from './component/ActionModal'
import AuditActionModal from './component/AuditActionModal'

import '@/assets/css/common.scss'
const { TabPane } = Tabs;
const { confirm } = Modal;

var statekey = '';
function Application(props){
    const history = useHistory();

    const columns = [
        {
            title: '社群编号',
            dataIndex: 'communityNumber',
            key: 'communityNumber',
        },
        {
            title: '排序权重',
            dataIndex: 'sort',
            key: 'sort',
        },
        {
            title: '名称',
            dataIndex: 'name',
            key: 'name',
        },
        {
            title: '图标',
            dataIndex: 'iconName',
            key: 'iconName',
        },
        {
            title: '状态',
            dataIndex: 'state',
            key: 'state',
            render: (text) => {
                return  text==0 ? '上架':'下架'
            }
        },
        {
            title: '推广文案',
            dataIndex: 'treatment',
            key: 'treatment',
        },
    {
        title: '操作',
            key: 'action',
        render: (text, record) => (
        <div className="action-button-group">

            <Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showAuditActionModal('add','操作',record)}}>操作</Button>&nbsp;
            <Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showUpdateModal('add','操作',record)}} >编辑</Button>&nbsp;
            <Button className="btn-edit" icon={<EditFilled />} onClick={()=>{showConfirm('add','操作',record)}} >删除</Button>

        </div>
    ),
    },
    ]; 
    const [dataList,setDataList] = useState([]);
    const [appListLoading,setAppListLoading] = useState(false);
    const [isShowModalVisible,setIsShowModalVisible] = useState(false);
    const [isShowAuditModalVisible,setIsShowAuditModalVisible] = useState(false);

    const [modalTitle,setModalTitle] = useState('');
    const [rowRecord,setRowRecord] = useState({});
    const [pagination,setPagination] = useState({
        current: 1,
        pageSize: 10
    });
    const getAppList = () => {
        setAppListLoading(true);
        console.log(statekey)
        axiosPost('/manage/wx/minipro/community/list',{
            current: pagination.current,
            pageSize: pagination.pageSize,
            type: statekey
        }).then((res) => {
            setAppListLoading(false);
            setDataList(res.data.list);
            setPagination({
                current: res.data.pageNum,
                pageSize: res.data.pageSize,
                total: res.data.total                
            })
        }).catch((error) => {
            console.log(error);
        });
    }
    const handleTableChange = (page) => {
        setPagination({
            ...page
        })
    }
    useEffect(() => {
        getAppList();

        return () => {
            
        }
    },[pagination.current]);

    const showAuditActionModal = (type,title,record) => {
        setIsShowAuditModalVisible(true);
        setModalTitle(title);
        setRowRecord(record);
        setAppListLoading(false);

    }
    const handleClick =(e) => {
        console.log('click ', e);
        this.setState({
            current: e.key,
        });
    };
    const hideModal = () => {
        setIsShowModalVisible(false);
        setIsShowAuditModalVisible(false);
    };
    function showAddModal(){
        history.push("/wx/addcommunitys")
    }
    const showUpdateModal = (type,title,record) => {

        setRowRecord(record);
        history.push({
            pathname: '/wx/updatecommunitys',
            state: { record },
        })

    }
    function callback(key) {

        console.log(statekey);
        statekey = key;
        getAppList();
    }
    const showConfirm = (type,title,record) => {
        confirm({
            title: '确定删除吗？',
            icon: <ExclamationCircleOutlined />,
            onOk() {
                axiosPost('/manage/wx/minipro/community/delete/'+record.id).then((res) => {
                    if(res.code == '0'){
                        message.success("操作成功");
                        hideModal();
                        getAppList();
                    }else{
                        message.error(res.message);
                    }
                }).catch((error) => {
                    console.log(error);
                });
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }
    return (
        <div className="application-box">
            <PageHeader title="全部社群" icon={AndroidFilled}  />
            <div className="page-body">
                <div className="panel">
                    <div className="panel-title">全部社群</div>
                    <div className="panel-body">
                        <div className="action">
                            <Button className="btn-add" icon={<PlusOutlined />} onClick={showAddModal} >添加应用</Button>
                        </div>
                        <Tabs defaultActiveKey={statekey} onChange={callback} type="card">
                        <TabPane tab="全部社群" key="">
                            <div className="panel-body">

                                <Table
                                    bordered
                                    loading={appListLoading}
                                    dataSource={dataList}
                                    columns={columns}
                                    rowKey={(row) => {
                                        return row.id
                                    }}
                                    pagination={{
                                        current: pagination.current,
                                        pageSize: pagination.pageSize,
                                        total: pagination.total
                                    }}
                                    onChange={handleTableChange}
                                />
                            </div>
                        </TabPane>
                        <TabPane tab="知识内容社群" key="10">
                            <div className="panel-body">
                                {/* <div className="action">
                            <Button className="btn-add" icon={<PlusOutlined />} onClick={()=>{showActionModal('add','新增应用')}}>添加应用</Button>
                        </div> */}
                                <Table
                                    bordered
                                    loading={appListLoading}
                                    dataSource={dataList}
                                    columns={columns}
                                    rowKey={(row) => {
                                        return row.id
                                    }}
                                    pagination={{
                                        current: pagination.current,
                                        pageSize: pagination.pageSize,
                                        total: pagination.total
                                    }}
                                    onChange={handleTableChange}
                                />
                            </div>
                        </TabPane>
                        <TabPane tab="在线教育社群" key="20">
                            <div className="panel-body">
                                {/* <div className="action">
                            <Button className="btn-add" icon={<PlusOutlined />} onClick={()=>{showActionModal('add','新增应用')}}>添加应用</Button>
                        </div> */}
                                <Table
                                    bordered
                                    loading={appListLoading}
                                    dataSource={dataList}
                                    columns={columns}
                                    rowKey={(row) => {
                                        return row.id
                                    }}
                                    pagination={{
                                        current: pagination.current,
                                        pageSize: pagination.pageSize,
                                        total: pagination.total
                                    }}
                                    onChange={handleTableChange}
                                />
                            </div>
                        </TabPane>
                        <TabPane tab="商务交流群" key="30">
                        <div className="panel-body">
                            {/* <div className="action">
                            <Button className="btn-add" icon={<PlusOutlined />} onClick={()=>{showActionModal('add','新增应用')}}>添加应用</Button>
                        </div> */}
                            <Table
                                bordered
                                loading={appListLoading}
                                dataSource={dataList}
                                columns={columns}
                                rowKey={(row) => {
                                    return row.id
                                }}
                                pagination={{
                                    current: pagination.current,
                                    pageSize: pagination.pageSize,
                                    total: pagination.total
                                }}
                                onChange={handleTableChange}
                            />
                        </div>
                    </TabPane>
                    </Tabs>
                    </div>                  
                </div>
            </div>
            <ActionModal visible={isShowModalVisible} title={modalTitle} record={rowRecord} hideModal={hideModal} refresh={getAppList} />
            <AuditActionModal visible={isShowAuditModalVisible} title={modalTitle} record={rowRecord} hideModal={hideModal} refresh={getAppList} />
        </div>
    )
}
const mapStateToProps = (state) => ({username: state.LoginReducer.username})
export default connect(mapStateToProps)(Application)