import React, {useEffect, useState} from 'react';
import PageHeader from '@/component/PageHeader'
import { useHistory} from 'react-router-dom'
import {
    Form,
    Input,
    Button,
    Radio,
    Select,
    Cascader,
    DatePicker,
    InputNumber,
    TreeSelect,
    Switch,
     message
} from 'antd';
import {connect} from "react-redux";
import { axiosPost} from '@/extend/axios/index.js'
import axios from 'axios'
import '@/assets/css/common.scss'
import {AndroidFilled,UploadOutlined } from "@ant-design/icons";
function Application(props){
    const [componentSize, setComponentSize] = useState('large');
    const record = props.location.state.record;
    console.log(props.location.state.record)
    console.log(record)

    const [form] = Form.useForm();
    const [visible,setVisible] = useState(record.shareWay)
    const history = useHistory();
    const onFinish = values => {
        console.log(values)
        axiosPost('/manage/wx/minipro/community/update',{
                    id:record.id,
                    type:values.type,
                    iconName:values.iconName,
                    name:values.name,
                    treatment:values.treatment,
                    sort:values.sort,
                    state:values.state,
                    welfare:values.welfare,
                    profile:values.profile,
                    specialGroup:values.specialGroup,
                    announcements:values.announcements,
                    adminWechatCode:values.adminWechatCode,
                    articleUrl:values.articleUrl,
                    articleImgUrl:values.articleImgUrl,
                    shareNumber:values.shareNumber,
                    joinNum:values.joinNum,
                    shareWay:values.shareWay,
                    payAmount:values.payAmount,
                    communityPeoples:values.communityPeoples

        }).then((res) => {
            if(res.code == '0'){
                        message.success("操作成功");
                        history.push("/wx/allcommunitys")
                    }else{
                        message.error(res.message);
                    }
        }).catch((error) => {
            console.log(error);
        });
    };
    const onChange = (e)=>{
        console.log(e.target.value)
        console.log("onchange:")
        setVisible(e.target.value);
        console.log(visible)
    }
    useEffect(() => {
        form.setFieldsValue(record);
    },[record]);
    return (
        <div className="application-box">
            <PageHeader title="修改" icon={AndroidFilled} />
            <div className="page-body">
                <div className="panel">
                    <div className="panel-body">
                        <Form
                            labelCol={{ span: 4 }}
                            wrapperCol={{ span: 14 }}
                            layout="horizontal"
                            size={componentSize}
                            form={form}
                            onFinish={onFinish}
                        >

                            <Form.Item label="类型" name="type"   rules={[{ required: true }]}>
                                <Radio.Group >
                                    <Radio value={10}>知识内容社群</Radio>
                                    <Radio value={20}>在线教育社群</Radio>
                                    <Radio value={30}>商务交流群</Radio>
                                </Radio.Group>
                            </Form.Item>
                            <Form.Item label="社群头像名" name="iconName" rules={[{ required: true, message: '请输入图标名称!' }]}>
                                <Input type="text"  />
                            </Form.Item>
                            <Form.Item label="社群名称" name="name" rules={[{ required: true, message: '请输入名称!' }]}>
                                <Input />
                            </Form.Item>
                            <Form.Item label="群介绍" name="profile" rules={[{ required: true, message: '请输入群介绍!' }]}>
                                <Input.TextArea placeholder="社群展示页面" />
                            </Form.Item>
                            <Form.Item label="加入人数" name="joinNum" rules={[{type: 'number', min: 1, required: true, message: '请输入加入人数!' }]} >
                                <InputNumber  />
                            </Form.Item>
                            <Form.Item name="sort" label="排序" rules={[{ type: 'number', min: 0,required: true, message: '请输入序号!'}]} >
                                <InputNumber />
                            </Form.Item>
                            <Form.Item label="状态" name="state" rules={[{ required: true, message: '请选择状态!' }]}>
                                <Radio.Group>
                                    <Radio value={0}>上架</Radio>
                                    <Radio value={1}>下架</Radio>
                                </Radio.Group>
                            </Form.Item>
                            <Form.Item label="群内福利" name="welfare" rules={[{ required: true, message: '请输入群内福利!' }]}>
                                <Input.TextArea placeholder="立即进群页面" />
                            </Form.Item>
                            <Form.Item label="特定人群" name="specialGroup" rules={[{ required: true, message: '请输入特定人群!' }]}>
                                <Input.TextArea placeholder="立即进群页面" />
                            </Form.Item>
                            <Form.Item label="注意事项" name="announcements" rules={[{ required: true, message: '请输入注意事项!' }]}>
                                <Input.TextArea placeholder="立即进群页面" />
                            </Form.Item>
                            <Form.Item label="入群文案" name="treatment" rules={[{ required: true, message: '请输入入群文案!' }]}>
                                <Input.TextArea placeholder="社群展示页面" />
                            </Form.Item>
                            <Form.Item label="管理员微信号" name="adminWechatCode" rules={[{ required: true, message: '请输入管理员微信号!' }]} >
                                <Input placeholder="进群方法页面" />
                            </Form.Item>
                            <Form.Item label="公众号文章地址" name="articleUrl" rules={[{ required: true, message: '请输入管理员微信号!' }]} >
                                <Input placeholder="微信公众号文章地址" />
                            </Form.Item>
                            <Form.Item label="公众号图片地址" name="articleImgUrl" rules={[{message: '请输入管理员微信号!' }]} >
                                <Input placeholder="阿里OSS地址" />
                            </Form.Item>
                            <Form.Item label="群成员人数" name="communityPeoples" rules={[{ required: true, message: '请输入群成员人数!' }]} >
                                <Input placeholder="群成员人数" />
                            </Form.Item>
                            <Form.Item label="分享方式" onChange={onChange} name="shareWay" rules={[{ required: true, message: '请选择分享方式!' }]}>
                                <Radio.Group>
                                    <Radio value={0}>全部</Radio>
                                    <Radio value={1}>方式一</Radio>
                                    <Radio value={2}>方式二</Radio>
                                </Radio.Group>
                            </Form.Item>
                            {visible==0||visible==1?(
                                <Form.Item label="分享次数" name="shareNumber" rules={[{type: 'number', min: 1, required: true, message: '请输入需分享次数!' }]} >
                                    <InputNumber  />
                                </Form.Item>
                            ):''}
                            {visible==0||visible==2 ?(
                                <Form.Item label="支付金额" name="payAmount" rules={[{ required: true, message: '请输入支付金额!' }]} >
                                    <Input placeholder="支付金额" />
                                </Form.Item>
                            ):''}
                            <Form.Item wrapperCol={{ offset: 10 }}>
                                <Button type="primary" htmlType="submit">
                                    提交
                                </Button>

                            </Form.Item>

                        </Form>

                    </div>
                </div>
            </div>
        </div>
    );
};
export default Application